import PropTypes from 'prop-types';
import { useState } from 'react';
import './chart.scss';
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
} from 'recharts';
import {
  DATA_CONFIG,
  STATIC_DATA as data,
} from '../../../shared/lib/config/data-config';
import useWindowSize from '../../../shared/lib/hooks/use-window-size';
import calculateWidthContainer from '../../../shared/lib/utils/calculate-width-container';
import ChipsList from '../../../shared/ui/chips-list/chips-list';
import CustomTooltip from './custom-tooltip/custom-tooltip';

/**
 * React компонент графика
 * @param {boolean} isShow - Булев тип, определяющий видимость React компонента;
 */

function Chart({ isShow }) {
  const [view, setView] = useState('temp');

  const selectedUnit = DATA_CONFIG.find((item) => item.id === view);

  const { width } = useWindowSize();

  const customizedAxisTick = ({ x, y, payload }) => {
    return (
      <g transform={`translate(${x},${y})`}>
        <text
          className='chart__text'
          transform='rotate(-45)'
          textAnchor='end'
          dominantBaseline='central'>
          {payload.value}
        </text>
      </g>
    );
  };

  const legendFormatter = () => {
    return <span className='chart__legend'>{selectedUnit.unit}</span>;
  };

  return (
    <div className={`chart${isShow ? ' chart_show' : ''}`}>
      <div className='chart__menu'>
        <ChipsList
          chips={DATA_CONFIG.map((i) => {
            return { ...i, isDisabled: view === i.label };
          })}
          textSize='large'
          value={view}
          onChange={(chips) => setView(chips)}
        />
      </div>
      <div className='chart__container'>
        <AreaChart
          width={calculateWidthContainer(width, 531, 444)}
          height={300}
          data={data}>
          <defs>
            <linearGradient
              id={`color${selectedUnit.id}`}
              x1='0'
              y1='0'
              x2='0'
              y2='1'>
              <stop
                offset='5%'
                stopColor={selectedUnit.color}
                stopOpacity={0.8}
              />
              <stop
                offset='95%'
                stopColor={selectedUnit.color}
                stopOpacity={0}
              />
            </linearGradient>
          </defs>
          <CartesianGrid stroke='var(--chart-accent)' strokeDasharray='3 3' />
          <XAxis
            stroke='var(--chart-accent)'
            dataKey='id'
            label={{ value: 'Участки', position: 'insideBottom', offset: 0 }}
            height={43}
            tick={customizedAxisTick}
          />
          <YAxis
            stroke='var(--chart-accent)'
            type='number'
            tick={customizedAxisTick}
          />
          <Tooltip
            isAnimationActive={false}
            content={<CustomTooltip selectedUnit={selectedUnit} />}
          />
          <Legend
            wrapperStyle={{ marginLeft: '28px' }}
            formatter={legendFormatter}
            align='left'
            verticalAlign='top'
            height={36}
          />
          <Area
            type='monotone'
            dataKey={selectedUnit.id}
            stroke={selectedUnit.color}
            fillOpacity={1}
            fill={`url(#color${selectedUnit.id})`}
          />
        </AreaChart>
      </div>
    </div>
  );
}

Chart.propTypes = {
  isShow: PropTypes.bool.isRequired,
};

export default Chart;
