import PropTypes from 'prop-types';
import { useCallback } from 'react';
import './chips-list.scss';
import Chip from '../chip/chip';

/**
 * React компонент cписка chips
 * @param {array} chips - Массив, содержит параметры для элементов chip;
 * @param {string} textSize - Строка, размер текста элементов chip;
 * @param {string} value - Строка, содержит смысловое значение для проверок;
 * @param {function} onChange - Коллбэк-функция, вызываемая при изменении cписка chips. Аргументом получает смысловое значение ввиде строки;
 */

function ChipsList({ chips, textSize, value, onChange }) {
  const onChipActivation = useCallback(
    (id) => {
      onChange(id);
    },
    [onChange]
  );

  return (
    <nav className='chips-list'>
      {chips.map((chip) => (
        <Chip
          key={chip.id}
          text={chip.label}
          isDisabled={chip.isDisabled}
          isActive={value === chip.id}
          setActive={() => onChipActivation(chip.id)}
          tooltipText={chip.label}
          indicatorColor={chip.color}
          textSize={textSize}
        />
      ))}
    </nav>
  );
}

ChipsList.propTypes = {
  chips: PropTypes.array.isRequired,
  textSize: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
};

export default ChipsList;
