import PropTypes from 'prop-types';
import './constructor.scss';
import CrossIcon from '../../../shared/assets/icons/cross-icon';
import NodeDeleteIcon from '../../../shared/assets/icons/node-delete-icon';
import {
  CONSTRUCTOR_WIDTH,
  PANEL_MARGIN_LEFT,
} from '../../../shared/lib/config/app-config';
import nodeTypeData from '../../../shared/lib/mock-data/node-data.json';
import Accordion from '../../../shared/ui/accordion/accordion';
import AccordionItem from '../../../shared/ui/accordion-item/accordion-item';
import BoxHeader from '../../../shared/ui/box-header/box-header';
import Button from '../../../shared/ui/button/button';
import Panel from '../../../shared/ui/panel/panel';
import Node from './node/node';

/**
 * React компонент конструктора блок-схемы ReactFlow
 * @param {boolean} isOpened - Булев тип, определяющий видимость React компонента;
 * @param {function} onAddNodes - Коллбэк-функция, вызываемая при отправке формы добавления. Аргументом получает количество ReactFlow компонентов;
 * @param {function} onDelete - Коллбэк-функция, вызываемая при нажатии на кнопку "Удалить";
 * @param {function} onClose - Коллбэк-функция, вызываемая при нажатии на кнопку "Закрыть";
 * @param {boolean} isValidDelete - Булев тип, определяющий состояние валидности удаления ReactFlow компонентов;
 */

function Constructor({
  isOpened,
  onAddNodes,
  onDelete,
  onClose,
  isValidDelete,
}) {
  return (
    <Panel
      isOpened={isOpened}
      width={`${CONSTRUCTOR_WIDTH}px`}
      marginTop='12px'
      marginLeft={`${PANEL_MARGIN_LEFT}px`}>
      <BoxHeader title={'Конструктор'} type='panel'>
        <Button
          style={{
            minWidth: '10px',
            minHeight: '10px',
          }}
          onClick={onClose}
          tooltipText='Закрыть конструктор'>
          <CrossIcon color='var(--text-accent)' />
        </Button>
      </BoxHeader>
      <Accordion title='Узлы' titleColor='var(--text-accent)'>
        {nodeTypeData.map((params, index) => (
          <Node key={index} params={params} onAddNodes={onAddNodes} />
        ))}
      </Accordion>
      <Accordion title='Инструменты' titleColor='var(--text-accent)'>
        <AccordionItem
          title='Удалить'
          description='Удалить последний узел'
          onClick={onDelete}
          tooltipText={
            isValidDelete
              ? 'Удалить узел'
              : 'Удалить можно только последний узел'
          }
          isInactive={!isValidDelete}>
          <div className='constructor__delete'>
            <Button isInactive={!isValidDelete} onClick={onDelete}>
              <NodeDeleteIcon isInactive={!isValidDelete} />
            </Button>
          </div>
        </AccordionItem>
      </Accordion>
    </Panel>
  );
}

Constructor.propTypes = {
  isOpened: PropTypes.bool.isRequired,
  onAddNodes: PropTypes.func.isRequired,
  onDelete: PropTypes.func.isRequired,
  onClose: PropTypes.func.isRequired,
  isValidDelete: PropTypes.bool.isRequired,
};

export default Constructor;
