import './confirmation.scss';
import { useDispatch, useSelector } from 'react-redux';
import CleanIcon from '../../../shared/assets/icons/clean-icon';
import CrossIcon from '../../../shared/assets/icons/cross-icon';
import { isPanelOpen, setPanelClose } from '../../../shared/model/panel-slice';
import BoxHeader from '../../../shared/ui/box-header/box-header';
import Button from '../../../shared/ui/button/button';
import ButtonWithBorder from '../../../shared/ui/button-with-border/button-with-border';
import Popup from '../../../shared/ui/popup/popup';

/**
 * React компонент подтверждения
 * @param {function} setIsClean - Коллбэк-функция, вызываемая при очистке рабочего пространства;
 */

function Confirmation({ setIsClean }) {
  const dispath = useDispatch();

  const isOpenConfirmation = useSelector((state) =>
    isPanelOpen(state, 'confirmation')
  );

  const handleClean = () => {
    setIsClean(true);
    dispath(setPanelClose('confirmation'));
  };

  return (
    <Popup
      isOpened={isOpenConfirmation}
      setOpened={() => dispath(setPanelClose('confirmation'))}>
      <BoxHeader type='popup'>
        <Button
          style={{
            minWidth: '10px',
            minHeight: '10px',
          }}
          onClick={() => dispath(setPanelClose('confirmation'))}
          tooltipText='Закрыть подтверждение очистки'>
          <CrossIcon color='var(--text-accent)' />
        </Button>
      </BoxHeader>
      <div className='confirmation'>
        <div className='confirmation__icon'>
          <CleanIcon color='var(--element-accent)' />
        </div>
        <p className='confirmation__caption'>Вы уверены?</p>
        <ButtonWithBorder
          style={{
            position: 'sticky',
            bottom: 0,
            left: 0,
            boxSizing: 'border-box',
          }}
          text='Да!'
          tooltipText='Да!'
          onClick={handleClean}
        />
      </div>
    </Popup>
  );
}

export default Confirmation;
