import PropTypes from 'prop-types';
import CrossIcon from '../../../shared/assets/icons/cross-icon';
import BoxHeader from '../../../shared/ui/box-header/box-header';
import Button from '../../../shared/ui/button/button';
import FormBuilder from '../../../shared/ui/form-builder/form-builder';
import Popup from '../../../shared/ui/popup/popup';

/**
 * React компонент ввода входных данных
 * @param {object} component - Объект ReactFlow компонента;
 * @param {function} onClose - Коллбэк-функция, вызываемая при нажатии на кнопку "Закрыть";
 */

function InputData({ component, onClose }) {
  const isNotEmpty = Object.keys(component).length;

  if (!isNotEmpty) {
    return;
  }

  return (
    <Popup isOpened={component.data.fields.length !== 0} setOpened={onClose}>
      <BoxHeader title={`Входные данные: ${component.data.label}`} type='popup'>
        <Button
          style={{
            minWidth: '10px',
            minHeight: '10px',
          }}
          onClick={onClose}
          tooltipText='Закрыть входные данные'>
          <CrossIcon color='var(--text-accent)' />
        </Button>
      </BoxHeader>
      <FormBuilder params={component.data.fields} />
    </Popup>
  );
}

InputData.propTypes = {
  component: PropTypes.object.isRequired,
  onClose: PropTypes.func.isRequired,
};

export default InputData;
