import PropTypes from 'prop-types';

export default function CrossIcon({ color }) {
  return (
    <svg
      xmlns='http://www.w3.org/2000/svg'
      width='10px'
      height='11px'
      fill='none'>
      <path stroke={color} d='m1 1.25 9 9.5M10 1.25l-9 9.5' />
    </svg>
  );
}

CrossIcon.propTypes = {
  color: PropTypes.string.isRequired,
};
