import PropTypes from 'prop-types';
import { useEffect, createRef } from 'react';
import './popup.scss';

function Popup({ children, isOpened, setOpened }) {
  const popupRef = createRef();

  useEffect(() => {
    const closeByEscape = (e) => {
      if (e.key === 'Escape') {
        setOpened(false);
      }
    };

    document.addEventListener('keydown', closeByEscape);
    return () => document.removeEventListener('keydown', closeByEscape);
  }, [isOpened, setOpened]);

  useEffect(() => {
    const handleFocus = (e) => {
      if (popupRef.current && !popupRef.current.contains(e.relatedTarget)) {
        popupRef.current.focus();
      }
    };

    if (isOpened) {
      document.addEventListener('focusout', handleFocus);
    }
    return () => document.removeEventListener('focusout', handleFocus);
  }, [isOpened, setOpened, popupRef]);

  useEffect(() => {
    if (isOpened) {
      document.body.style.overflow = 'hidden';
    }
    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [isOpened]);

  const handleOverlay = (e) => {
    if (e.target === e.currentTarget) {
      setOpened(false);
    }
  };

  return (
    <div>
      <div
        data-testid='popup'
        className={isOpened ? 'popup popup_open' : 'popup'}
        role='button'
        tabIndex={0}
        onMouseDown={handleOverlay}
        ref={popupRef}>
        <div className='popup__content'>{children}</div>
      </div>
    </div>
  );
}

Popup.propTypes = {
  children: PropTypes.any,
  isOpened: PropTypes.bool.isRequired,
  setOpened: PropTypes.func.isRequired,
};

export default Popup;
