import PropTypes from 'prop-types';
import './button.scss';

/**
 * React компонент кнопки
 * @param {string} text - Строка, текст кнопки;
 * @param {object} style - Объект, стили кнопки;
 * @param {function} onClick - Коллбэк-функция, вызываемая при нажатии на кнопку;
 * @param {boolean} isInactive - Булев тип, определяющий состояние активности кнопки;
 * @param {string} tooltipText - Строка, текст всплывающей подсказки кнопки;
 * @param {string} hoverStyle - Строка, определяющая стиль при наведении на кнопку;
 */

function Button({
  children,
  className,
  text,
  style,
  onClick,
  isInactive,
  tooltipText,
  hoverStyle,
}) {
  const customClassName = `button button_hover_${hoverStyle || 'opacity'}`;

  return (
    <button
      className={`${customClassName}${isInactive ? ' button_inactive' : ''} ${className || ''}`}
      style={style}
      onClick={onClick}
      disabled={isInactive}
      title={tooltipText}>
      <div className='button__icon'>{children}</div>
      {text}
    </button>
  );
}

Button.propTypes = {
  children: PropTypes.any,
  text: PropTypes.string,
  style: PropTypes.object,
  onClick: PropTypes.func,
  isInactive: PropTypes.bool,
  tooltipText: PropTypes.string,
  hoverStyle: PropTypes.string,
};

export default Button;
