import PropTypes from 'prop-types';
import { useCallback, useEffect, useState } from 'react';
import ReactFlow, {
  useNodesState,
  useEdgesState,
  addEdge,
  useStore,
  useReactFlow,
  useKeyPress,
  Background,
  BackgroundVariant,
  getOutgoers,
} from 'reactflow';
import 'reactflow/dist/style.css';
import './flowchart.scss';
import './react-flow.scss';
import edgeData from '../../../shared/lib/mock-data/edge-data.json';
import CustomControls from './custom-controls/custom-controls';
import CustomEdge from './custom-edge/custom-edge';
import CustomInput from './custom-input/custom-input';
import CustomNode from './custom-node/custom-node';

// Определение кастомных узлов и рёбер
const nodeTypes = {
  'custom-input': CustomInput,
  'custom-node': CustomNode,
};

const edgeTypes = {
  'custom-edge': CustomEdge,
};

/**
 * React компонент блок-схемы ReactFlow
 * @param {boolean} isShow - Булев тип, определяющий видимость React компонента;
 * @param {boolean} isSave - Булев тип, определяющий состояние сохранения;
 * @param {function} setIsSave - Коллбэк-функция, вызываемая для сброса сохранения до состояния "false";
 * @param {function} setComponent - Коллбэк-функция, вызываемая для передачи объекта ReactFlow компонента. Аргументом получает объект ReactFlow компонента;
 * @param {object} nodeData - Объект, ...;
 * @param {function} setNodeData - Коллбэк-функция, ...;
 * @param {object} csvFile - Объект с CSV;
 * @param {object} addedNode - Объект, представляющий данные узла ReactFlow;
 * @param {function} setAddedNode - Коллбэк-функция, вызываемая для сброса данных добавленного узла ReactFlow;
 * @param {boolean} isDelete - Булев тип, определяющий состояние удаления ReactFlow компонентов на области просмотра;
 * @param {function} setDeleteState - Коллбэк-функция, вызываемая для сброса удаления ReactFlow компонентов до состояния "false";
 * @param {boolean} isClean - Булев тип, определяющий состояние очистки области просмотра ReactFlow;
 * @param {function} setIsClean - Коллбэк-функция, вызываемая для сброса очистки области просмотра ReactFlow до состояния "false";
 * @param {boolean} isValidDelete - Булев тип, определяющий состояние валидности удаления ReactFlow компонентов;
 * @param {function} setIsValidDelete - Коллбэк-функция, вызываемая для сброса валидности удаления ReactFlow компонентов до состояния "false";
 */

function Flowchart({
  isShow,
  isSave,
  setIsSave,
  setComponent,
  nodeData,
  setNodeData,
  csvFile,
  addedNode,
  setAddedNode,
  isDelete,
  setDeleteState,
  isClean,
  setIsClean,
  isValidDelete,
  setIsValidDelete,
}) {
  // Получение текущего уровня масштабирования области просмотра ReactFlow
  const isMaxZoom = useStore((store) => store.transform[2] === store.maxZoom);
  const isMinZoom = useStore((store) => store.transform[2] === store.minZoom);

  // Cостояния для хранения узлов и рёбер
  const [nodes, setNodes, onNodesChange] = useNodesState([]);
  const [edges, setEdges, onEdgesChange] = useEdgesState([]);

  // Cостояние для хранения выбранного узла
  const [selectedNode, setSelectedNode] = useState(null);

  // Состояние кнопки 'Shift'
  const isShiftPressed = useKeyPress('Shift');

  // Состояние кнопки 'Delete'
  const isDeletePressed = useKeyPress('Delete');

  const [reactFlowInstance, setReactFlowInstance] = useState(null);

  // Поиск свободного id узлов и рёбер
  function findFreeId(arr, prefix) {
    const filteredArr = arr.filter(
      (item) => item.id.startsWith(prefix) && /^\w+\d+$/.test(item.id)
    );

    if (filteredArr.length === 0) {
      return 1;
    }

    const sortedArr = filteredArr
      .map((item) => {
        const numericPart = parseInt(item.id.slice(1));
        return isNaN(numericPart) ? null : numericPart;
      })
      .filter((id) => id !== null)
      .sort((a, b) => a - b);

    for (let i = 1; i <= sortedArr[sortedArr.length - 1]; i++) {
      if (sortedArr.indexOf(i) === -1) {
        return i;
      }
    }

    return sortedArr[sortedArr.length - 1] + 1;
  }

  // Очищение области просмотра React Flow от узлов и участков
  const onClean = useCallback(() => {
    setNodes([]);
    setEdges([]);
    setComponent({});
    setIsClean(false);
  }, []);

  // Обработка выбора узлов
  const onNodeClick = useCallback(
    (event, node) => {
      setComponent(node);
      setSelectedNode(node);
    },
    [edges]
  );

  const onEdgeClick = useCallback(
    (event, edge) => {
      setComponent(edge);
      setSelectedNode(null);
      setIsValidDelete(false);
    },
    [nodes]
  );

  // Сохранение входных данных в узле
  const handleSave = useCallback(
    (data) => {
      if (Object.keys(data).length && selectedNode) {
        setNodes((nds) => {
          nds.map((node) =>
            node.id === selectedNode.id ? { ...node, data } : node
          );
        });
        setComponent({});
        setNodeData({});
      }
    },
    [selectedNode]
  );

  // По клику на пустое место области просмотра React Flow
  const onPaneClick = () => {
    setComponent({});
  };

  // По соединению двух узлов
  const onConnect = useCallback(
    (params) =>
      setEdges((prevEdges) => {
        const currentIdEdge = findFreeId(prevEdges, 'e');

        return addEdge(
          {
            id: 'e' + currentIdEdge,
            ...params,
            type: 'custom-edge',
            data: {
              label: `Участок ${currentIdEdge}`,
              fields: edgeData.fields,
            },
          },
          prevEdges
        );
      }),
    []
  );

  // Добавление узла по клику
  const onAddNode = useCallback(
    (params) => {
      setNodes((prevNodes) => {
        const currentIdNode = findFreeId(prevNodes, 'n');
        const isInputOrOutput =
          params.type === 'input' || params.type === 'output';

        const newNodes = [
          {
            id: 'n' + currentIdNode,
            sourcePosition: 'right',
            targetPosition: 'left',
            type: `custom-${isInputOrOutput ? params.type : 'node'}`,
            data: {
              label: `${params.name} ${currentIdNode}`,
              type: params.type,
              fields: params.fields ? params.fields : [],
            },
            position: {
              x: 0,
              y: 0,
            },
            connectable: true,
          },
        ];
        return [...prevNodes, ...newNodes];
      });
      setAddedNode({});
    },
    [edges]
  );

  // Добавление узлов по drag-on-drop
  const onDragOver = useCallback((event) => {
    event.preventDefault();
    event.dataTransfer.dropEffect = 'move';
  }, []);

  const onDrop = useCallback(
    (event) => {
      event.preventDefault();

      const params = JSON.parse(
        event.dataTransfer.getData('application/reactflow')
      );

      if (typeof params.type === 'undefined' || !params) {
        return;
      }

      const position = reactFlowInstance.screenToFlowPosition({
        x: event.clientX,
        y: event.clientY,
      });
      const currentIdNode = findFreeId(nodes, 'n');
      const isInputOrOutput =
        params.type === 'input' || params.type === 'output';

      const newNode = {
        id: 'n' + currentIdNode,
        sourcePosition: 'right',
        targetPosition: 'left',
        type: `custom-${isInputOrOutput ? params.type : 'node'}`,
        data: {
          label: `${params.name} ${currentIdNode}`,
          type: params.type,
          fields: params.fields ? params.fields : [],
        },
        position,
        connectable: true,
      };

      setNodes((nds) => nds.concat(newNode));
    },
    [reactFlowInstance, nodes]
  );

  // Удаление узла по кнопке в конструкторе и DEL
  const { deleteElements } = useReactFlow();

  const handleDelete = useCallback(() => {
    if (selectedNode) {
      const targetEdges = edges.filter((e) => e.target === selectedNode.id);

      if (targetEdges.length === 0 && isValidDelete) {
        deleteElements({
          nodes: [selectedNode],
        });
      }
      if (targetEdges.length !== 0 && isValidDelete) {
        deleteElements({
          nodes: [selectedNode],
          edges: targetEdges,
        });
      }
      if (isValidDelete) {
        setSelectedNode(null);
        setComponent({});
        setIsValidDelete(false);
        setAddedNode({});
      }
    }

    setDeleteState(false);
  }, [deleteElements, selectedNode, isValidDelete, edges]);

  // Импорт данных из CSV документа
  const importFromCsv = useCallback((file) => {
    console.log(file);
  }, []);

  // Экспорт данных в CSV документ
  const exportToCsv = useCallback(() => {
    console.log('Экспорт данных');
    setIsSave(false);
  }, [nodes, edges]);

  // Импорт данных из CSV документа по открытию файла
  useEffect(() => {
    if (csvFile) importFromCsv(csvFile);
  }, [csvFile, importFromCsv]);

  // Экспорт данных в CSV документ по клику в тулбаре
  useEffect(() => {
    if (isSave) exportToCsv();
  }, [isSave]);

  // Сохранение данных узла
  useEffect(() => {
    handleSave(nodeData);
  }, [nodeData]);

  // Добавление узлов по клику в конструкторе
  useEffect(() => {
    if (addedNode.type) onAddNode(addedNode);
  }, [addedNode]);

  // Удаление узла по клику в конструкторе или по нажатию DEL
  useEffect(() => {
    if (isDelete || isDeletePressed) handleDelete();
  }, [isDelete, isDeletePressed]);

  // Валидация удаления и добавления
  useEffect(() => {
    if (selectedNode) {
      const outgoers = getOutgoers(selectedNode, nodes, edges);
      setIsValidDelete(outgoers.length === 0);
    }
  }, [selectedNode, nodes, edges]);

  // Очищение области просмотра React Flow по клику в конструкторе
  useEffect(() => {
    if (isClean) onClean();
  }, [isClean]);

  // Открытие панели Constructor и сброс выбора при изменении видимости области просмотра ReactFlow
  useEffect(() => {
    if (nodes.length === 0) {
      setSelectedNode(null);
    }
  }, [nodes, isShow]);

  return (
    <div className={`flowchart${isShow ? ' flowchart_show' : ''}`}>
      <ReactFlow
        nodes={nodes}
        edges={edges}
        connectionLineType='straight'
        onNodesChange={onNodesChange}
        onEdgesChange={onEdgesChange}
        onConnect={onConnect}
        onNodeClick={onNodeClick}
        onEdgeClick={onEdgeClick}
        onPaneClick={onPaneClick}
        deleteKeyCode={null}
        snapToGrid={true}
        snapGrid={[15, 15]}
        nodeTypes={nodeTypes}
        edgeTypes={edgeTypes}
        onInit={setReactFlowInstance}
        onDrop={onDrop}
        onDragOver={onDragOver}
        fitView
        attributionPosition='bottom-left'>
        <CustomControls
          isShiftPressed={isShiftPressed}
          isMaxZoom={isMaxZoom}
          isMinZoom={isMinZoom}
        />
        <Background
          gap={[15, 15]}
          color='var(--element-inactive-accent)'
          variant={BackgroundVariant.Cross}
        />
      </ReactFlow>
    </div>
  );
}

Flowchart.propTypes = {
  isShow: PropTypes.bool.isRequired,
  isSave: PropTypes.bool.isRequired,
  setIsSave: PropTypes.func.isRequired,
  setComponent: PropTypes.func.isRequired,
  nodeData: PropTypes.object.isRequired,
  setNodeData: PropTypes.func.isRequired,
  csvFile: PropTypes.object,
  addedNode: PropTypes.object.isRequired,
  setAddedNode: PropTypes.func.isRequired,
  isDelete: PropTypes.bool.isRequired,
  setDeleteState: PropTypes.func.isRequired,
  isClean: PropTypes.bool.isRequired,
  setIsClean: PropTypes.func.isRequired,
  isValidDelete: PropTypes.bool.isRequired,
  setIsValidDelete: PropTypes.func.isRequired,
};

export default Flowchart;
