import PropTypes from 'prop-types';
import './node.scss';
import { BASE_URL } from '../../../../shared/lib/config/api-config';
import AccordionItem from '../../../../shared/ui/accordion-item/accordion-item';
import Button from '../../../../shared/ui/button/button';

const MODE = process.env.REACT_APP_MODE;

function Node({ params, onAddNodes }) {
  const pathToImg = `${BASE_URL}static/${params.type}.svg`;

  const handleClick = () => {
    onAddNodes(params);
  };

  const onDragStart = (e) => {
    const img = new Image();
    img.src = pathToImg;
    e.dataTransfer.setDragImage(img, 0, 0);

    e.dataTransfer.setData('application/reactflow', JSON.stringify(params));
    e.dataTransfer.effectAllowed = 'move';
  };

  return (
    <div className='node' onDragStart={(e) => onDragStart(e)} draggable>
      <AccordionItem
        title={params.name}
        description={params.description}
        tooltipText={`Добавить "${params.name}"`}
        onClick={handleClick}
        isInactive={false}>
        <div className='node__container'>
          <Button onClick={handleClick}>
            <img
              src={
                MODE === 'production'
                  ? pathToImg
                  : require(`../../../../static/${params.type}.svg`)
              }
              alt='Иконка узла'
            />
          </Button>
        </div>
      </AccordionItem>
    </div>
  );
}

Node.propTypes = {
  params: PropTypes.object.isRequired,
  onAddNodes: PropTypes.func.isRequired,
};

export default Node;
