import PropTypes from 'prop-types';
import { useState, useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import './workspace.scss';
import { useResizeDetector } from 'react-resize-detector';
import Chart from '../../../../features/chart/ui/chart';
import Flowchart from '../../../../features/flowchart/ui/flowchart';
import Table from '../../../../features/table/ui/table';
import {
  INDICATIONS_WIDTH,
  CONSTRUCTOR_WIDTH,
  PANEL_MARGIN_LEFT,
  BARS_HEIGHT,
} from '../../../../shared/lib/config/app-config';
import useWindowSize from '../../../../shared/lib/hooks/use-window-size';
import {
  isPanelOpen,
  setPanelOpen,
  setPanelClose,
} from '../../../../shared/model/panel-slice';
import { selectView } from '../../../../shared/model/view-slice';
import Confirmation from '../../../../widgets/confirmation/ui/confirmation';
import Constructor from '../../../../widgets/constructor/ui/constructor';
import Indications from '../../../../widgets/indications/ui/indications';
import InputData from '../../../../widgets/input-data/ui/input-data';
import View from '../../../../widgets/view/ui/view';

/**
 * React компонент рабочего пространства
 * @param {boolean} isSave - Булев тип, определяющий состояние сохранения;
 * @param {function} setIsSave - Коллбэк-функция, вызываемая для сброса сохранения до состояния "false";
 * @param {object} csvFile - Объект с CSV;
 */

function Workspace({ isSave, setIsSave, csvFile }) {
  const dispatch = useDispatch();
  const isOpenedConstructor = useSelector((state) =>
    isPanelOpen(state, 'constructor')
  );
  const isOpenedIndications = useSelector((state) =>
    isPanelOpen(state, 'indications')
  );
  const view = useSelector((state) => selectView(state));

  const [component, setComponent] = useState({});
  const [data, setData] = useState({});
  const [isOpenedPanels, setIsOpenedPanels] = useState(
    Object.keys(component).length || isOpenedConstructor
  );
  const [addedNode, setAddedNode] = useState({});
  const [isDelete, setIsDelete] = useState(false);
  const [isValidDelete, setIsValidDelete] = useState(false);
  const [isClean, setIsClean] = useState(false);

  const panelsDisplacement = isOpenedPanels
    ? 0
    : (isOpenedIndications ? INDICATIONS_WIDTH + PANEL_MARGIN_LEFT : 0) +
      (isOpenedConstructor ? CONSTRUCTOR_WIDTH + PANEL_MARGIN_LEFT : 0);

  const { height: windowHeight } = useWindowSize();

  const { height: panelsHeight, ref: panelsRef } = useResizeDetector({
    refreshMode: 'debounce',
    refreshRate: 100,
  });

  const contentRef = useRef();

  useEffect(() => {
    const isFlowchartView = view === 'flowchart';

    setIsOpenedPanels(isFlowchartView);
  }, [view]);

  useEffect(() => {
    const compensatedHeight = windowHeight - BARS_HEIGHT;

    if (compensatedHeight < panelsHeight)
      contentRef.current.style.height = `${panelsHeight}px`;
    else contentRef.current.style.height = `${compensatedHeight}px`;
  }, [windowHeight, panelsHeight]);

  return (
    <section className='workspace'>
      <div className='workspace__content' ref={contentRef}>
        <Flowchart
          isShow={view === 'flowchart'}
          isSave={isSave}
          setIsSave={setIsSave}
          setComponent={(component) => setComponent(component)}
          nodeData={data}
          setNodeData={(nodeData) => setData(nodeData)}
          setIsOpenedConstructor={(boolean) =>
            boolean
              ? dispatch(setPanelOpen('constructor'))
              : dispatch(setPanelClose('constructor'))
          }
          csvFile={csvFile}
          addedNode={addedNode}
          setAddedNode={(params) => setAddedNode(params)}
          isDelete={isDelete}
          setDeleteState={(boolean) => setIsDelete(boolean)}
          isClean={isClean}
          setIsClean={(boolean) => setIsClean(boolean)}
          isValidDelete={isValidDelete}
          setIsValidDelete={(boolean) => setIsValidDelete(boolean)}
        />
        <InputData
          component={component}
          onClose={() => setComponent({})}
          onSave={(data) => setData(data)}
        />
        <Confirmation setIsClean={(boolean) => setIsClean(boolean)} />
      </div>
      <div className='workspace__panels' ref={panelsRef}>
        <div
          style={{ left: `-${panelsDisplacement}px` }}
          className='workspace__control'>
          <Constructor
            isOpened={isOpenedConstructor}
            onAddNodes={(node) => setAddedNode(node)}
            onDelete={() => setIsDelete(true)}
            onClose={() => dispatch(setPanelClose('constructor'))}
            isValidDelete={isValidDelete}
          />
          <Indications
            isOpened={isOpenedIndications}
            onClose={() => dispatch(setPanelClose('indications'))}
          />
        </div>
        <div
          style={{ left: `-${panelsDisplacement}px` }}
          className='workspace__view'>
          <View />
          <Table isShow={view === 'table'} />
          <Chart isShow={view === 'chart'} />
        </div>
      </div>
    </section>
  );
}

Workspace.propTypes = {
  isSave: PropTypes.bool.isRequired,
  setIsSave: PropTypes.func.isRequired,
  csvFile: PropTypes.object,
};

export default Workspace;
