import PropTypes from 'prop-types';
import './chip.scss';
import IndicatorIcon from '../../assets/icons/indicator-icon';

/**
 * React компонент элемента chip
 * @param {string} text - Строка, текст элемента chip;
 * @param {boolean} isDisabled - Булев тип, определяющий работоспособности элемента chip;
 * @param {boolean} isActive - Булев тип, определяющий состояние активности элемента chip;
 * @param {function} setActive - Коллбэк-функция, вызываемая при нажатии на элемент chip. Аргументом получает "true";
 * @param {string} tooltipText - Строка, текст всплывающей подсказки элемента chip;
 * @param {string} indicatorColor - Строка, цвет индикатора элемента chip;
 * @param {string} textSize - Строка, размер текста элемента chip. Размеры: 'small', 'large';
 */

function Chip({
  text,
  isDisabled,
  isActive,
  setActive,
  tooltipText,
  indicatorColor,
  textSize,
}) {
  return (
    <button
      type='button'
      disabled={isDisabled}
      className={`chip ${isActive ? 'chip_active' : ''} chip_text-size_${textSize}`}
      onClick={() => setActive(true)}
      title={tooltipText}>
      {text}
      {indicatorColor && (
        <div className='chip__indicator'>
          <IndicatorIcon color={indicatorColor} />
        </div>
      )}
    </button>
  );
}

Chip.propTypes = {
  text: PropTypes.string.isRequired,
  isDisabled: PropTypes.bool.isRequired,
  isActive: PropTypes.bool.isRequired,
  setActive: PropTypes.func.isRequired,
  tooltipText: PropTypes.string.isRequired,
  indicatorColor: PropTypes.string,
  textSize: PropTypes.string.isRequired,
};

export default Chip;
