import PropTypes from 'prop-types';
import './form-builder.scss';
import { useState } from 'react';
import Form from 'react-bootstrap/Form';
import ButtonWithBorder from '../button-with-border/button-with-border';

/**
 * React компонент строитель форм
 * @param {array} params - Массив, содержит параметры формы;
 */

const FormBuilder = ({ params }) => {
  const [formValues, setFormValues] = useState({});
  const [validated, setValidated] = useState(false);

  const handleChange = (e) => {
    setFormValues({
      ...formValues,
      [e.target.id]: e.target.value,
    });
  };

  const handleSubmit = (event) => {
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    }
    if (form.checkValidity()) {
      event.preventDefault();
      event.stopPropagation();
      setFormValues({});
      console.log(JSON.stringify(formValues));
    }

    setValidated(true);
  };

  return (
    <Form
      className='form-builder'
      noValidate
      validated={validated}
      onSubmit={handleSubmit}>
      {params.map((input, index) => (
        <Form.Group
          className='form-builder__group'
          controlId={input.id}
          key={index}>
          <Form.Label className='form-builder__label' style={{ margin: 0 }}>
            {input.label}
          </Form.Label>
          <Form.Control
            className='form-builder__input'
            required
            type='number'
            placeholder={input.placeholder}
            value={formValues[input.id] || ''}
            onChange={handleChange}
          />
        </Form.Group>
      ))}
      <ButtonWithBorder
        type='submit'
        style={{
          margin: '12px 0 0',
          position: 'sticky',
          bottom: 0,
          left: 0,
          boxSizing: 'border-box',
        }}
        text='Применить'
      />
    </Form>
  );
};

FormBuilder.propTypes = {
  params: PropTypes.array.isRequired,
};

export default FormBuilder;
