import { createSlice, createSelector } from '@reduxjs/toolkit';

const initialState = {
  panels: [],
};

const panelsSlice = createSlice({
  name: 'panels',
  initialState,
  reducers: {
    setPanelOpen: (store, { payload }) => ({
      panels: [...store.panels, payload],
    }),
    setPanelClose: (store, { payload }) => ({
      panels: store.panels.filter((x) => x !== payload),
    }),
  },
});

export const isPanelOpen = createSelector(
  [(state) => state.panels, (_, panelId) => panelId],
  (panels, panelId) => panels.panels.some((x) => x === panelId)
);

export const { setPanelOpen, setPanelClose } = panelsSlice.actions;

export default panelsSlice.reducer;
