import PropTypes from 'prop-types';
import './status.scss';

function Status({ color, percent, state, context }) {
  return (
    <div className='status'>
      <div className='status__donut' style={{ backgroundColor: color }}>
        <div className='status__donut-inner' />
        <span
          className='status__percent'
          style={{ color }}>{`${percent}%`}</span>
      </div>
      <div className='status__text'>
        <h2 className='status__title'>Состояние:</h2>
        <h3 className='status__state' style={{ color }}>
          {state}
        </h3>
        <p className='status__state-context'>{context}</p>
      </div>
    </div>
  );
}

Status.propTypes = {
  color: PropTypes.string.isRequired,
  percent: PropTypes.number.isRequired,
  state: PropTypes.string.isRequired,
  context: PropTypes.string.isRequired,
};

export default Status;
