import PropTypes from 'prop-types';
import './custom-controls.scss';
import { useReactFlow } from 'reactflow';
import GrabIcon from '../../../../shared/assets/icons/grab-icon';
import PointerIcon from '../../../../shared/assets/icons/pointer-icon';
import ViewIcon from '../../../../shared/assets/icons/view-icon';
import ZoomInIcon from '../../../../shared/assets/icons/zoomin-icon';
import ZoomOutIcon from '../../../../shared/assets/icons/zoomout-icon';
import Button from '../../../../shared/ui/button/button';

const CustomControls = ({ isShiftPressed, isMaxZoom, isMinZoom }) => {
  const { zoomIn, zoomOut, fitView } = useReactFlow();

  return (
    <div className='custom-controls'>
      <Button onClick={zoomOut} tooltipText='Отдалить' isInactive={isMinZoom}>
        <ZoomOutIcon
          color='var(--black)'
          fillOpacity={isMinZoom ? '.10' : '.25'}
        />
      </Button>
      <Button onClick={zoomIn} tooltipText='Приблизить' isInactive={isMaxZoom}>
        <ZoomInIcon
          color='var(--black)'
          fillOpacity={isMaxZoom ? '.10' : '.25'}
        />
      </Button>
      {isShiftPressed ? (
        <Button
          style={{ minHeight: '22px' }}
          tooltipText='Отпустите Shift чтобы переместить'
          isInactive>
          <PointerIcon color='var(--black)' />
        </Button>
      ) : (
        <Button
          style={{ minHeight: '22px' }}
          tooltipText='Зажмите Shift чтобы выделить'
          isInactive>
          <GrabIcon color='var(--black)' />
        </Button>
      )}
      <Button onClick={fitView} tooltipText='Центрировать'>
        <ViewIcon />
      </Button>
    </div>
  );
};

CustomControls.propTypes = {
  isShiftPressed: PropTypes.bool.isRequired,
  isMaxZoom: PropTypes.bool.isRequired,
  isMinZoom: PropTypes.bool.isRequired,
};

export default CustomControls;
