import PropTypes from 'prop-types';
import { useState } from 'react';
import './value.scss';
import { useResizeDetector } from 'react-resize-detector';
import IndicatorIcon from '../../assets/icons/indicator-icon';

/**
 * React компонент величины
 * @param {string} name - Строка, название величины;
 * @param {number} meaning - Число, значение величины;
 * @param {number} meaningPlusMinus - Число, значение плюс-минус величины;
 * @param {string} unit - Строка, единица измерения величины;
 * @param {string} indicatorColor - Строка, цвет индикатора величины;
 * @param {string} description - Строка, описание величины;
 */

function Value({
  children,
  name,
  meaning,
  meaningPlusMinus,
  unit,
  indicatorColor,
  description,
}) {
  const [isOpenedDropdownList, setIsOpenedDropdownList] = useState(false);

  const { height: descriptionHeight, ref: descriptionRef } = useResizeDetector({
    refreshMode: 'debounce',
    refreshRate: 100,
  });

  return (
    <div className={`value${isOpenedDropdownList ? ' value_dropdown' : ''}`}>
      <button
        onClick={() => setIsOpenedDropdownList(!isOpenedDropdownList)}
        title='Подробнее'
        className='value__button'>
        <div className='value__indication'>
          {children}
          <h3 className='value__name'>{name}</h3>
          <div
            className={`value__row-container${!isOpenedDropdownList ? ' value__row-container_show' : ''}`}>
            <p className='value__meaning'>{meaning}</p>
            <span className='value__unit'>{unit}</span>
            <IndicatorIcon color={indicatorColor} />
          </div>
        </div>
        <p className='value__description' ref={descriptionRef}>
          {description}
        </p>
      </button>
      <div
        className={`value__dropdown-container${isOpenedDropdownList ? ' value__dropdown-container_show' : ''}`}
        style={
          isOpenedDropdownList
            ? { top: 18 + descriptionHeight + 'px' }
            : { top: 0 }
        }>
        <p className='value__text'>Значение:</p>
        <p className='value__meaning'>{meaning}</p>
        <span className='value__unit'>{unit}</span>
        <p className='value__meaning'>{`± ${meaningPlusMinus}`}</p>
        <IndicatorIcon color={indicatorColor} />
      </div>
    </div>
  );
}

Value.propTypes = {
  children: PropTypes.any,
  name: PropTypes.string.isRequired,
  meaning: PropTypes.number.isRequired,
  meaningPlusMinus: PropTypes.number.isRequired,
  unit: PropTypes.string.isRequired,
  indicatorColor: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
};

export default Value;
