import './main.scss';
import { useState } from 'react';
import StatusBar from '../../../widgets/status-bar/ui/status-bar';
import Toolbar from '../../../widgets/tool-bar/ui/tool-bar';
import Workspace from './workspace/workspace';

function Main() {
  const [isSave, setIsSave] = useState(false);
  const [csvFile, setCsvFile] = useState(null);

  return (
    <main className='main'>
      <Toolbar
        onSave={() => setIsSave(true)}
        csvFile={(data) => setCsvFile(data)}
      />
      <Workspace
        isSave={isSave}
        setIsSave={(boolean) => setIsSave(boolean)}
        csvFile={csvFile}
      />
      <StatusBar />
    </main>
  );
}

export default Main;
