import PropTypes from 'prop-types';
import { useState, useRef } from 'react';
import './tool-bar.scss';
import { useDispatch, useSelector } from 'react-redux';
import CalculatorIcon from '../../../shared/assets/icons/calculator-icon';
import ConstructorIcon from '../../../shared/assets/icons/constructor-icon';
import DataIcon from '../../../shared/assets/icons/data-icon';
import ExportIcon from '../../../shared/assets/icons/export-icon';
import FileIcon from '../../../shared/assets/icons/file-icon';
import ImportIcon from '../../../shared/assets/icons/import-icon';
import InfoIcon from '../../../shared/assets/icons/info-icon';
import OpenFileIcon from '../../../shared/assets/icons/open-file-icon';
import RedoIcon from '../../../shared/assets/icons/redo-icon';
import RemoveIcon from '../../../shared/assets/icons/remove-icon';
import SaveIcon from '../../../shared/assets/icons/save-icon';
import UndoIcon from '../../../shared/assets/icons/undo-icon';
import {
  isPanelOpen,
  setPanelOpen,
  setPanelClose,
} from '../../../shared/model/panel-slice';
import { selectView } from '../../../shared/model/view-slice';
import Button from '../../../shared/ui/button/button';
import Divider from './divider/divider';

/**
 * React компонент панели инструментов
 * @param {function} onSave - Коллбэк-функция, вызываемая при нажатии на кнопку "Сохранить";
 * @param {function} csvFile - Коллбэк-функция, вызываемая при импорте CSV. Аргументом получает объект CSV файла;
 */

function Toolbar({ onSave, csvFile }) {
  const dispatch = useDispatch();
  const isOpenedConstructor = useSelector((state) =>
    isPanelOpen(state, 'constructor')
  );
  const isOpenedIndications = useSelector((state) =>
    isPanelOpen(state, 'indications')
  );
  const isOpenConfirmation = useSelector((state) =>
    isPanelOpen(state, 'confirmation')
  );

  const isNotFlowchartView =
    'flowchart' !== useSelector((state) => selectView(state));

  const [isCalculate] = useState(false);

  const openFileInputRef = useRef(null);
  const importFileInputRef = useRef(null);

  const handleOpenFileClick = () => {
    openFileInputRef.current.click();
  };

  const handleImportFileClick = () => {
    importFileInputRef.current.click();
  };

  return (
    <section className='tool-bar'>
      <div className='tool-bar__actions-group'>
        <h2 className='tool-bar__project-title'>Название проекта</h2>
        <Divider style={{ margin: '0 13px' }} />
        <div className='tool-bar__btns-group'>
          <Button
            text='Создать'
            style={{
              minWidth: '34px',
              minHeight: '33px',
            }}
            tooltipText='Создать проект'>
            <FileIcon color='var(--element-accent)' />
          </Button>
          <Button
            text='Открыть'
            style={{
              minWidth: '34px',
              minHeight: '33px',
            }}
            onClick={handleOpenFileClick}
            tooltipText='Открыть проект'>
            <input
              className='tool-bar__file-input'
              type='file'
              accept='.excel'
              ref={openFileInputRef}
              onChange={(e) => csvFile(e.target.files[0])}
            />
            <OpenFileIcon color='var(--element-accent)' />
          </Button>
          <Button
            text='Очистить'
            style={{
              minWidth: '34px',
              minHeight: '33px',
            }}
            onClick={() =>
              isOpenConfirmation
                ? dispatch(setPanelClose('confirmation'))
                : dispatch(setPanelOpen('confirmation'))
            }
            tooltipText='Очистить блок схему'>
            <RemoveIcon color='var(--element-accent)' />
          </Button>
          <Button
            text='О проекте'
            style={{
              minWidth: '34px',
              minHeight: '33px',
            }}
            tooltipText='Изменить информацию о проекте'>
            <InfoIcon color='var(--element-accent)' />
          </Button>
          <Divider style={{ margin: '0 13px' }} />
          <Button
            text='Импорт'
            style={{
              minWidth: '34px',
              minHeight: '33px',
            }}
            tooltipText='Импортировать данные'
            onClick={handleImportFileClick}>
            <input
              className='tool-bar__file-input'
              type='file'
              ref={importFileInputRef}
            />
            <ImportIcon color='var(--element-accent)' />
          </Button>
          <Button
            text='Экспорт'
            style={{
              minWidth: '34px',
              minHeight: '33px',
            }}
            tooltipText='Экспортировать данные'>
            <ExportIcon color='var(--element-accent)' />
          </Button>
          <Button
            text='Сохранить'
            style={{
              minWidth: '34px',
              minHeight: '33px',
            }}
            onClick={onSave}
            tooltipText='Сохранить в CSV'>
            <SaveIcon
              width='19px'
              height='19px'
              color='var(--element-accent)'
            />
          </Button>
          <Button
            text='Рассчитать'
            style={{
              minWidth: '34px',
              minHeight: '33px',
            }}
            isInactive={!isCalculate}
            tooltipText='Рассчитать входные данные'>
            <CalculatorIcon
              color={
                isCalculate
                  ? 'var(--element-accent)'
                  : 'var(--element-inactive-accent)'
              }
            />
          </Button>
          <Divider style={{ margin: '0 13px' }} />
          <Button
            text='Отмена'
            style={{
              minWidth: '34px',
              minHeight: '33px',
            }}
            tooltipText='Отмена действия'>
            <UndoIcon color='var(--element-accent)' />
          </Button>
          <Button
            text='Повтор'
            style={{
              minWidth: '34px',
              minHeight: '33px',
            }}
            tooltipText='Повтор действия'>
            <RedoIcon color='var(--element-accent)' />
          </Button>
          <Divider style={{ margin: '0 13px' }} />
          <Button
            text='Конструктор'
            style={{
              minWidth: '34px',
              minHeight: '33px',
            }}
            tooltipText='Открыть конструктор'
            onClick={() => dispatch(setPanelOpen('constructor'))}
            isInactive={isOpenedConstructor || isNotFlowchartView}>
            <ConstructorIcon
              color={
                isOpenedConstructor || isNotFlowchartView
                  ? 'var(--element-inactive-accent)'
                  : 'var(--element-accent)'
              }
            />
          </Button>
          <Button
            text='Выходные данные'
            style={{
              minWidth: '34px',
              minHeight: '33px',
            }}
            tooltipText='Открыть выходные данные'
            onClick={() => dispatch(setPanelOpen('indications'))}
            isInactive={isOpenedIndications || isNotFlowchartView}>
            <DataIcon
              color={
                isOpenedIndications || isNotFlowchartView
                  ? 'var(--element-inactive-accent)'
                  : 'var(--element-accent)'
              }
            />
          </Button>
        </div>
      </div>
    </section>
  );
}

Toolbar.propTypes = {
  onSave: PropTypes.func.isRequired,
  csvFile: PropTypes.func.isRequired,
};

export default Toolbar;
