import PropTypes from 'prop-types';
import './table.scss';
import { useRowSelect, useSortBy, useTable } from 'react-table';
import ArrowDownIcon from '../../../shared/assets/icons/arrow-down-icon';
import IndicatorIcon from '../../../shared/assets/icons/indicator-icon';
import { STATIC_DATA as data } from '../../../shared/lib/config/data-config';
import useWindowSize from '../../../shared/lib/hooks/use-window-size';
import calculateWidthContainer from '../../../shared/lib/utils/calculate-width-container';
import BoxHeader from '../../../shared/ui/box-header/box-header';

const columns = [
  {
    Header: 'Участок',
    accessor: 'id',
    Cell: ({ value }) => `Участок ${value}`,
  },
  {
    Header: 'Температура',
    accessor: 'temp',
    Cell: ({ value }) => (
      <div className='table__cell-container'>
        <IndicatorIcon color='var(--normal-accent)' />
        <p className='table__cell-meaning'>{value}</p>
        <p className='table__cell-meaning'>{'± 12'}</p>
      </div>
    ),
  },
  {
    Header: 'Давление',
    accessor: 'pressure',
    Cell: ({ value }) => (
      <div className='table__cell-container'>
        <IndicatorIcon color='var(--normal-accent)' />
        <p className='table__cell-meaning'>{value}</p>
        <p className='table__cell-meaning'>{'± 12'}</p>
      </div>
    ),
  },
  {
    Header: 'КННС',
    accessor: 'knns',
    Cell: ({ value }) => (
      <div className='table__cell-container'>
        <IndicatorIcon color='var(--normal-accent)' />
        <p className='table__cell-meaning'>{value}</p>
        <p className='table__cell-meaning'>{'± 12'}</p>
      </div>
    ),
  },
  {
    Header: 'Эррозия',
    accessor: 'erosion',
    Cell: ({ value }) => (
      <div className='table__cell-container'>
        <IndicatorIcon color='var(--normal-accent)' />
        <p className='table__cell-meaning'>{value}</p>
        <p className='table__cell-meaning'>{'± 12'}</p>
      </div>
    ),
  },
  {
    Header: 'Коррозия',
    accessor: 'corrosion',
    Cell: ({ value }) => (
      <div className='table__cell-container'>
        <IndicatorIcon color='var(--normal-accent)' />
        <p className='table__cell-meaning'>{value}</p>
        <p className='table__cell-meaning'>{'± 12'}</p>
      </div>
    ),
  },
];

/**
 * React компонент таблицы
 * @param {boolean} isShow - Булев тип, определяющий видимость React компонента;
 */

function Table({ isShow }) {
  const { getTableProps, getTableBodyProps, headerGroups, rows, prepareRow } =
    useTable(
      {
        columns,
        data,
      },
      useSortBy,
      useRowSelect
    );

  const { width } = useWindowSize();

  return (
    <div
      style={{
        width: `${calculateWidthContainer(width, 240, 786)}px`,
      }}
      className={`table${isShow ? ' table_show' : ''}`}>
      <BoxHeader title='Список участков газопровода' type='panel' />
      <table className='table__container' {...getTableProps()}>
        <thead>
          {headerGroups.map((headerGroup, rowIndex) => (
            <tr {...headerGroup.getHeaderGroupProps()} key={rowIndex}>
              {headerGroup.headers.map((column) => (
                <th
                  className='table__header'
                  {...column.getHeaderProps(() => {
                    const props = column.getSortByToggleProps();
                    const nextProps = {};
                    for (const key in props) {
                      if (key === 'title') {
                        nextProps[key] = 'Сортировать колонку';
                      } else {
                        nextProps[key] = props[key];
                      }
                    }
                    return nextProps;
                  })}
                  key={column.id}>
                  {column.render('Header')}
                  <span className='table__checkmark'>
                    {column.isSorted ? (
                      column.isSortedDesc ? (
                        <ArrowDownIcon color='var(--black-50)' isInverted />
                      ) : (
                        <ArrowDownIcon
                          color='var(--black-50)'
                          isInverted={false}
                        />
                      )
                    ) : (
                      ''
                    )}
                  </span>
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {rows.map((row) => {
            prepareRow(row);
            return (
              <tr className='table__row' {...row.getRowProps()} key={row.id}>
                {row.cells.map((cell, cellIndex) => (
                  <td
                    className='table__cell'
                    {...cell.getCellProps()}
                    key={cellIndex}>
                    {cell.render('Cell')}
                  </td>
                ))}
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}

Table.propTypes = {
  isShow: PropTypes.bool.isRequired,
};

export default Table;
