import PropTypes from 'prop-types';
import { useState } from 'react';
import './accordion.scss';
import ArrowDownIcon from '../../../shared/assets/icons/arrow-down-icon';
import Button from '../../../shared/ui/button/button';

/**
 * React компонент аккордеона
 * @param {string} title - Строка, заголовок аккордеона;
 * @param {string} titleColor - Строка, цвет заголовка аккордеона;
 */

function Accordion({ children, title, titleColor }) {
  const [isShow, setIsShow] = useState(true);

  return (
    <div className='accordion'>
      <div className='accordion__header'>
        <h2 className='accordion__title' style={{ color: titleColor }}>
          {title}
        </h2>
        {!isShow && (
          <Button
            style={{
              minWidth: '7px',
              minHeight: '4px',
            }}
            onClick={() => setIsShow(true)}
            tooltipText='Развернуть список'>
            <ArrowDownIcon color='var(--black-50)' isInverted />
          </Button>
        )}
        {isShow && (
          <Button
            style={{
              minWidth: '7px',
              minHeight: '4px',
            }}
            onClick={() => setIsShow(false)}
            tooltipText='Свернуть список'>
            <ArrowDownIcon color='var(--black-50)' isInverted={false} />
          </Button>
        )}
      </div>
      {isShow && <div className='accordion__content'>{children}</div>}
    </div>
  );
}

Accordion.propTypes = {
  children: PropTypes.any,
  title: PropTypes.string.isRequired,
  titleColor: PropTypes.string.isRequired,
};

export default Accordion;
