import PropTypes from 'prop-types';
import './custom-input.scss';
import { memo } from 'react';
import { Handle, Position, useEdges } from 'reactflow';

const CustomInput = memo(({ id, data, selected }) => {
  const edges = useEdges();

  const isSourceConnected = edges.some((i) => i.source === id);

  return (
    <div
      title='Вход'
      className={`custom-input${selected ? ' custom-input_selected' : ''}`}>
      <p className='custom-input__label'>{data.label}</p>
      <Handle
        isConnectable={true}
        className={`custom-input__handle-right${
          isSourceConnected
            ? selected
              ? ' custom-input__handle-right_connected custom-input__handle-right_selected'
              : ' custom-input__handle-right_connected'
            : selected
              ? ' custom-input__handle-right_selected'
              : ''
        }`}
        type='source'
        position={Position.Right}
      />
    </div>
  );
});

CustomInput.displayName = 'CustomInput';

CustomInput.propTypes = {
  id: PropTypes.string.isRequired,
  data: PropTypes.object.isRequired,
  selected: PropTypes.bool.isRequired,
};

export default CustomInput;
