import PropTypes from 'prop-types';
import './panel.scss';

/**
 * React компонент панели
 * @param {boolean} isOpened - Булев тип, определяющий видимость React компонента;
 * @param {string} width - Строка, ширина панели;
 * @param {string} marginTop - Строка, верхний внешний отступ панели;
 * @param {string} marginLeft - Строка, левый внешний отступ панели;
 */

function Panel({ children, isOpened, width, marginTop, marginLeft }) {
  return (
    <div
      style={{ width, marginTop, marginLeft }}
      className={`panel${isOpened ? ' panel_open' : ''}`}>
      {children}
    </div>
  );
}

Panel.propTypes = {
  isOpened: PropTypes.bool,
  width: PropTypes.string,
  marginTop: PropTypes.string,
  marginLeft: PropTypes.string,
};

export default Panel;
