import { useDispatch, useSelector } from 'react-redux';
import './view.scss';
import { setView, selectView } from '../../../shared/model/view-slice';
import ChipsList from '../../../shared/ui/chips-list/chips-list';
import Panel from '../../../shared/ui/panel/panel';

function View() {
  const dispatch = useDispatch();
  const view = useSelector((state) => selectView(state));

  return (
    <Panel isOpened width='min-content' marginTop='12px'>
      <h2 className='view__title'>Вид</h2>
      <ChipsList
        chips={[
          {
            label: 'Блок схема',
            id: 'flowchart',
            isDisabled: view === 'flowchart',
          },
          {
            label: 'Таблица',
            id: 'table',
            isDisabled: view === 'table',
          },
          {
            label: 'Графики',
            id: 'chart',
            isDisabled: view === 'chart',
          },
        ]}
        textSize='small'
        value={view}
        onChange={(view) => dispatch(setView(view))}
      />
    </Panel>
  );
}

export default View;
