export const STATIC_DATA = [];

// Количество данных
const quantityData = 10;

function getRandomNumber(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

for (let i = 1; i <= quantityData; i++) {
  STATIC_DATA.push({
    id: i,
    temp: getRandomNumber(1, 1000),
    pressure: getRandomNumber(1, 1000),
    knns: getRandomNumber(1, 1000),
    erosion: getRandomNumber(1, 1000),
    corrosion: getRandomNumber(1, 1000),
  });
}

