import PropTypes from 'prop-types';

export default function IndicatorIcon({ color }) {
  return (
    <svg
      style={{ margin: 'auto 0', overflow: 'visible' }}
      xmlns='http://www.w3.org/2000/svg'
      width='6'
      height='6'
      fill='none'>
      <rect width='6' height='6' fill={color} rx='3' />
    </svg>
  );
}

IndicatorIcon.propTypes = {
  color: PropTypes.string.isRequired,
};
