import PropTypes from 'prop-types';
import './box-header.scss';

function BoxHeader({ children, title, type }) {
  return (
    <div className={`box-header box-header_type_${type}`}>
      <h2 className='box-header__title'>{title}</h2>
      {children}
    </div>
  );
}

BoxHeader.propTypes = {
  children: PropTypes.any,
  title: PropTypes.string,
  type: PropTypes.string.isRequired,
};

export default BoxHeader;
