import PropTypes from 'prop-types';
import CorrosionIcon from '../../../shared/assets/icons/corrosion-icon';
import CrossIcon from '../../../shared/assets/icons/cross-icon';
import ErosionIcon from '../../../shared/assets/icons/erosion-icon';
import PressureIcon from '../../../shared/assets/icons/pressure-icon';
import PumpIcon from '../../../shared/assets/icons/pump-icon';
import TempIcon from '../../../shared/assets/icons/temp-icon';
import {
  INDICATIONS_WIDTH,
  PANEL_MARGIN_LEFT,
} from '../../../shared/lib/config/app-config';
import BoxHeader from '../../../shared/ui/box-header/box-header';
import Button from '../../../shared/ui/button/button';
import Panel from '../../../shared/ui/panel/panel';
import Status from '../../../shared/ui/status/status';
import Value from '../../../shared/ui/value/value';

/**
 * React компонент индикации выходных данных
 * @param {boolean} isOpened - Булев тип, определяющий видимость React компонента;
 * @param {function} onClose - Коллбэк-функция, вызываемая при нажатии на кнопку "Закрыть";
 */

function Indications({ isOpened, onClose }) {
  return (
    <Panel
      isOpened={isOpened}
      width={`${INDICATIONS_WIDTH}px`}
      marginTop='12px'
      marginLeft={`${PANEL_MARGIN_LEFT}px`}>
      <BoxHeader title={`Выходные данные:`} type='panel'>
        <Button
          style={{
            minWidth: '10px',
            minHeight: '10px',
          }}
          onClick={onClose}
          tooltipText='Закрыть выходные данные'>
          <CrossIcon color='var(--text-accent)' />
        </Button>
      </BoxHeader>
      <Status
        color='var(--normal-accent)'
        percent={100}
        state='Нормальное'
        context='Все показатели в норме'
      />
      <Value
        name='Температура'
        meaning={123}
        meaningPlusMinus={123}
        unit='°К'
        indicatorColor='var(--normal-accent)'
        description='Описание'>
        <TempIcon color='var(--icon-accent)' />
      </Value>
      <Value
        name='Давление'
        meaning={123}
        meaningPlusMinus={123}
        unit='Па'
        indicatorColor='var(--normal-accent)'
        description='Описание'>
        <PressureIcon color='var(--icon-accent)' />
      </Value>
      <Value
        name='КННС'
        meaning={123}
        meaningPlusMinus={123}
        unit='бр'
        indicatorColor='var(--normal-accent)'
        description='Описание'>
        <PumpIcon color='var(--icon-accent)' />
      </Value>
      <Value
        name='Эрозия'
        meaning={123}
        meaningPlusMinus={123}
        unit='мм / год'
        indicatorColor='var(--normal-accent)'
        description='Описание'>
        <ErosionIcon color='var(--icon-accent)' />
      </Value>
      <Value
        name='Коррозия'
        meaning={123}
        meaningPlusMinus={123}
        unit='мм / год'
        indicatorColor='var(--normal-accent)'
        description='Описание'>
        <CorrosionIcon color='var(--icon-accent)' />
      </Value>
    </Panel>
  );
}

Indications.propTypes = {
  isOpened: PropTypes.bool.isRequired,
  onClose: PropTypes.func.isRequired,
};

export default Indications;
