import PropTypes from 'prop-types';

export default function ArrowDownIcon({ color, isInverted }) {
  return (
    <svg
      style={{
        transform: isInverted ? 'rotateX(180deg)' : 'rotateX(0deg)',
      }}
      xmlns='http://www.w3.org/2000/svg'
      width='8px'
      height='5px'
      fill='none'>
      <path
        stroke={color}
        strokeLinecap='round'
        d='m1 1 2.646 2.646a.5.5 0 0 0 .708 0L7 1'
      />
    </svg>
  );
}

ArrowDownIcon.propTypes = {
  color: PropTypes.string.isRequired,
  isInverted: PropTypes.bool.isRequired,
};
