import PropTypes from 'prop-types';
import './custom-node.scss';
import { memo } from 'react';
import { Handle, Position, useEdges } from 'reactflow';
import { BASE_URL } from '../../../../shared/lib/config/api-config';

const MODE = process.env.REACT_APP_MODE;

const CustomNode = memo(({ id, selected, data }) => {
  const relativePathToImage = `static/${data.type}.svg`;

  const edges = useEdges();

  const isTargetConnected = edges.some((i) => i.target === id);
  const isSourceConnected = edges.some((i) => i.source === id);

  return (
    <div title='Узел' className='custom-node'>
      <div className='custom-node__dot-left'>
        <Handle
          isConnectable={true}
          className={`custom-node__handle-left${
            isTargetConnected
              ? selected
                ? ' custom-node__handle-left_connected custom-node__handle-left_selected'
                : ' custom-node__handle-left_connected'
              : selected
                ? ' custom-node__handle-left_selected'
                : ''
          }`}
          type='target'
          position={Position.Left}
        />
      </div>
      <div className='custom-node__content'>
        <img
          className='custom-node__icon'
          src={
            MODE === 'production'
              ? `${BASE_URL}${relativePathToImage}`
              : require(`../../../../${relativePathToImage}`)
          }
          alt='Иконка узла'
        />
      </div>
      <div className='custom-node__dot-right'>
        <Handle
          isConnectable={true}
          className={`custom-node__handle-right${
            isSourceConnected
              ? selected
                ? ' custom-node__handle-right_connected custom-node__handle-right_selected'
                : ' custom-node__handle-right_connected'
              : selected
                ? ' custom-node__handle-right_selected'
                : ''
          }`}
          type='source'
          position={Position.Right}
        />
      </div>
    </div>
  );
});

CustomNode.displayName = 'CustomNode';

CustomNode.propTypes = {
  id: PropTypes.string.isRequired,
  data: PropTypes.object.isRequired,
  selected: PropTypes.bool.isRequired,
};

export default CustomNode;
