import PropTypes from 'prop-types';
import './custom-edge.scss';
import { EdgeLabelRenderer, BaseEdge, getStraightPath } from 'reactflow';

const CustomEdge = ({
  id,
  data,
  sourceX,
  sourceY,
  targetX,
  targetY,
  selected,
}) => {
  const [edgePath, labelX, labelY] = getStraightPath({
    sourceX,
    sourceY,
    targetX,
    targetY,
  });

  return (
    <>
      <BaseEdge
        style={{ stroke: 'var(--node-border-accent)' }}
        interactionWidth={20}
        id={id}
        path={edgePath}
      />
      <EdgeLabelRenderer>
        <div
          style={{
            transform: `translate(-50%, -50%) translate(${labelX}px,${labelY}px)`,
          }}
          className={`custom-edge nodrag nopan${selected ? ' custom-edge_selected' : ''}`}>
          <p className='custom-edge__label'>{data.label}</p>
        </div>
      </EdgeLabelRenderer>
    </>
  );
};

CustomEdge.propTypes = {
  id: PropTypes.string.isRequired,
  data: PropTypes.object.isRequired,
  sourceX: PropTypes.number.isRequired,
  sourceY: PropTypes.number.isRequired,
  targetX: PropTypes.number.isRequired,
  targetY: PropTypes.number.isRequired,
  selected: PropTypes.bool.isRequired,
};

export default CustomEdge;
