/**
 * Утилита расчета ширины контейнера
 * @param {number} width - Число, ширина контейнера;
 * @param {number} value - Число, величина для расчёта;
 * @param {number} minWidth - Число, минимальная ширина контейнера;
 */

function calculateWidthContainer(width, value, minWidth) {
  const percent = value / 100;
  const product = percent * 9.8;

  const containerWidth = width - product;

  return containerWidth >= minWidth ? containerWidth : minWidth;
}

export default calculateWidthContainer;
